.qa__title .container {
  align-items: center;
  margin-bottom: 3rem;
}

.qa__title h1 {
  max-width: 500px;
  font-size: 2.5rem;
}

.qa__nav {
  box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  padding: 25px 0;
  max-width: 280px;
  font-weight: 600;
  font-size: 18px;
  position: sticky;
  top: 0;
  left: 0;

  & .nav-link.active,
  & .nav-link:focus,
  & .nav-link:hover,
  & .nav-link:target {
    color: #333333;
  }
}

.qa__accordion {

  & .accordion-item {
    border-color: #ccc;
  }

  &.accordion-flush .accordion-item:last-child {
    border-bottom: 1px solid #ccc;
  }

  & .accordion-button {
    padding-right: 0;
    padding-left: 0;
    font-size: 18px;
    font-weight: 600;
  }

  & .accordion-button.collapsed::after {
    background-image: url('../img/cross.svg');
    background-size: 24px;
    background-position: center;
  }

  & .accordion-button::after {
    background-image: url('../img/min.svg');
    background-size: 24px auto;
    background-position: center;
  }

  & .accordion-button:focus {
    border: none;
    box-shadow: none;
  }

  & .accordion-button:not(.collapsed) {
    color: inherit;
    background-color: inherit;
  }

  & .accordion-body {
    padding-right: 0;
    padding-left: 0;
    padding-top: 0;
    font-weight: 300;
  }
}

@media (max-width: 992px) {
  .qa__title {
    text-align: center;
  }

  .qa__title h1 {
    font-size: 36px;
  }
}

@media (max-width: 768px) {
  .qa__nav {
    margin: 0 auto 2rem;
    position: static;
  }
}
