.no-page {
  min-height: 655px;
  justify-content: space-between;
  background: #F6F9FB;
}

.no-page__header {
  background-color: $prime-color;
  box-shadow: 0px 4px 16px rgba(20, 122, 169, 0.16);

  & .header__logo {
    filter: brightness(0) invert(1);
  }
}

.no-page__main {
  flex: 1 1 auto;

  & h1 {
    font-size: 2rem;
  }
}

.no-page__main .container {
  background: url('../img/not.png') no-repeat top right;
  height: 100%;
  height: 100%;
  flex: 1 1 auto;
  justify-content: flex-end;
}

@media (max-width: 992px) {
  .no-page__main .container {
    background-size: 40% auto;
  }
}

@media (max-width: 768px) {
  .no-page__main .container {
    background-size: 50% auto;
  }
}

@media (max-width: 380px) {
  .no-page__main .container {
    background-size: auto 150px;
  }

  .no-page__main h1 {
    margin-top: 180px;
  }
}
