.price {
  margin: 100px 0;

  & h1 {
    font-size: 46px;
    margin-bottom: 20px;
    text-align: center;
  }

  & .form-check {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    font-weight: 600;
    padding-left: 0;
  }

  & .card-title, .h5 {
    font-weight: 700;
  }

  & .card {
    border-radius: 15px;
  }
}

.price__text {
  max-width: 550px;
  font-size: 20px;
  margin: 0 auto 50px;
}

#flexSwitchCheckDefault.price__toggle {
  margin: 0 10px;
  width: 60px;
  height: 30px;

  &:focus {
    box-shadow: none;
    border-color: #ccc;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='rgba%280, 0, 0, 0.25%29'/%3e%3c/svg%3e");
  }

  &:checked {
    border-color: #ccc;
    background-color: #fff;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23147AA9'/%3e%3c/svg%3e");
  }
}

@media (max-width: 768px) {
  .price h1 {
    font-size: 36px;
  }
}
