body {
  font-size: 16px;
  font-family: $regular-font;
  color: $text-color;
  margin: 0;
  padding: 0;
  min-width: 360px;
  height: 100%;
  line-height: 1.3;
  letter-spacing: 0.2px;
  background-color: #fff;
}

a {
  display: inline-block;
  transition: 0.3s;
  text-decoration: none;

  &:hover,
  &:focus {
    color: $prime-color;
  }
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  color: #333;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  border: none;
  padding: 0;
  background-color: transparent;
  box-shadow: none;
  cursor: pointer;
}
