.main__nav .nav-link {
  font-weight: 600;
  color: $text-color;

  &:hover, &:focus, &:active {
    color: $prime-color;
  }
}

.btn {
  font-weight: 600;
  border-width: 1.5px;

  &:focus {
    box-shadow: none;
  }
}

.exit {
  color: $prime-color;

  &:hover, &:focus, &:active {
    color: $text-color;
    box-shadow: none;
  }
}

.banner .container {
  padding-top: 50px;
  padding-bottom: 150px;
  background: url('../img/landing_banner.png') no-repeat right 150px top/auto 380px;
}

.banner__text {
  & h1 {
    font-weight: 700;
    font-size: 46px;
  }

  & p {
    font-size: 20px;
    margin: 25px 0 50px;
  }
}

.adv {
  & h2 {
    font-weight: 700;
    font-size: 2.2rem;
  }
}

.adv__text {
  max-width: 700px;
  margin: 20px auto 50px;
}

.adv__list {
  list-style: none;
  padding-left: 0;
  font-weight: 300;

  & h3 {
    font-weight: 600;
    padding-top: 90px;
    background-size: 70px auto;
    background-repeat: no-repeat;
    background-position: top center;
  }

  & li:nth-of-type(1) h3 {
    background-image: url('../img/stopwatch.svg');
  }

  & li:nth-of-type(2) h3 {
    background-image: url('../img/decision.svg');
  }

  & li:nth-of-type(3) h3 {
    background-image: url('../img/calendar.svg');
  }

  & li:nth-of-type(4) h3 {
    background-image: url('../img/organization.svg');
  }
}

.slider {
  padding: 100px 0;
  margin-bottom: 100px;
  background: url('../img/gantt.png') no-repeat center right -6%/auto 100%;
}

.carousel-text {
  & h3 {
    font-weight: 700;
    margin-bottom: 20px;
    max-width: 400px;
  }
}

.carousel-indicators {
  position: static;
  justify-content: flex-start;
  margin: 20px 0 0;
  align-items: center;
}

.carousel-indicators [data-bs-target] {
  width: 12px;
  height: 12px;
  background: #CCCCCC;
  border-radius: 50%;
  border: none;
  opacity: 1;
  margin-right: 12px;
}

.carousel-indicators .active {
  background: $prime-color;
}

.tasks .row {
  margin-bottom: 70px;

  & .tasks__img {
    height: 400px;
  }

  &:nth-of-type(1) .tasks__img {
    background: url('../img/cod.png') no-repeat center/contain;
  }

  &:nth-of-type(2) .tasks__img {
    background: url('../img/table.png') no-repeat center/contain;
  }

  &:nth-of-type(3) .tasks__img {
    background: url('../img/work.png') no-repeat center/contain;
  }
}

.tasks h3 {
  font-weight: 700;
}

.callback {
  background: $prime-color;
  padding: 60px 0;
  color: #fff;
  position: relative;
  margin-top: 210px;

  & h3 {
    color: #fff;
  }

  &::after {
    content: "";
    width: 50%;
    height: 512px;
    position: absolute;
    bottom: 0;
    left: 0;
    background: url('../img/rocket.png') no-repeat bottom left/contain;
  }
}

.callback__form {
  max-width: 45%;
  margin-left: auto;

  & p {
    max-width: 300px;
  }

  & form {
    position: relative;
  }

  & input[type="email"] {
    width: 100%;
    height: 44px;
    border-radius: 25px;
  }

  & .btn {
    position: absolute;
    top: 2px;
    right: 13px;
    border-radius: 25px;
    padding: 4px 20px 3px;
  }
}

#to-top {
  width: 44px;
  height: 44px;
  position: fixed;
  bottom: 30px;
  right: 5%;
  border-radius: 8px;
  background: $prime-color url('../img/up.svg') no-repeat center/16px 20px;
  display: none;
  transition: 0.3s;

  &:hover, &:focus {
    background-color: $hover-color;
  }
}

@media (max-width: 992px) {
  .header.open {
    background: $prime-color;
    color: #fff;

    .navbar {
      min-height: 100vh;
      align-items: flex-start;
    }

    & .navbar-collapse {
      margin-top: 50px;
    }

    & .header__logo {
      filter: brightness(0) invert(1);
    }

    & .nav-link {
      color: #fff;
      font-weight: 600;
      font-size: 24px;
      padding-left: 0 !important;
    }

    & .btn {
      width: 330px;
      max-width: 100%;
      color: #fff;
      border: 2px solid #fff;
      margin-bottom: 10px;
    }

    .exit {
      margin-right: 0 !important;
    }

    & .btn-outline-primary {
      order: -1;
      background: #fff;
      color: $prime-color;
      margin-top: 30px;
    }
  }

  .navbar-toggler span {
    width: 24px;
    height: 2px;
    background: $text-color;
    display: block;
    margin-bottom: 8px;
    transition: 0.5s;
  }

  .header.open span {
    background: #fff;
    transition: 0.5s;

    &:nth-of-type(1) {
      transform: rotate(45deg) translateY(7px);
    }

    &:nth-of-type(2) {
      transform: rotate(-45deg) translateY(-7px);
    }

    &:nth-of-type(3) {
      opacity: 0;
    }
  }

  .navbar-toggler {
    padding: 0;
  }

  .navbar-toggler:focus {
    box-shadow: none;
  }

  .container {
    padding-left: 16px;
    padding-right: 16px;
  }

  .banner .container {
    padding-top: 330px;
    background-size: auto 300px;
    background-position: top center;
    padding-bottom: 50px;
  }

  .banner__text h1 {
    font-size: 36px;
  }

  .banner__text p {
    margin-bottom: 25px;
  }

  .adv__list > li:not(:last-of-type) {
    margin-bottom: 20px;
  }

  .tasks .row {
    margin-bottom: 30px;
  }

  .callback::after {
    width: 40%;
  }

  .callback__form {
    max-width: 55%;
  }

  section {
    margin-bottom: 64px !important;
  }

  .tasks__img {
    background-size: 325px auto;
  }
}

@media (max-width: 768px) {
  .slider {
    padding: 300px 0 30px;
    background-position: top center;
    background-size: auto 270px;
    background-image: url('../img/gantt-mob.png');
  }

  .tasks__img {
    order: -1;
    margin-bottom: 50px;
  }

  .callback {
    margin-top: 0;

    &:after {
      display: none;
    }
  }

  .callback__form {
    max-width: 100%;

    & .btn {
      font-size: 16px;
      padding: 8px 20px 6px;
    }
  }

  .callback__form input[type="email"] {
    font-size: 16px;
    height: 45px;
  }

  .footer .col-0 {
    display: none;
    visibility: hidden;
  }
}

@media (min-width: 1400px) {
  .container, .container-lg,
  .container-md, .container-sm,
  .container-xl, .container-xxl {
    max-width: 1350px;
  }
}
